import React, { useState } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import Structure from '../Structure';
import { colors } from '@components/typo';
import {AiOutlineArrowRight, AiOutlineArrowLeft} from 'react-icons/ai'
import './index.css';

import CompanySearch from './CompanySearch';
import ScamSearch from './ScamSearch';

const SwitchButton = styled.div`
    padding: 10px;
    min-width: 100px;
    max-width: 265px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ff6600;
    color: #fff;
    border-radius: 5px;
    box-shadow: 0px 4px 18px -4px rgb(255 102 0 / 46%);
    border-radius: 5px;
`;

const AgreementBox = styled.div`
    margin-top: 10px;
    font-size: 13px;
`;

const Links = styled(Link)`
    color: ${colors.defaultColor};
`;

const Description = () => {
    return (
        <div>
            <div>Scan for FRAUDULENT Documents, Phone Number and Email</div>
            <AgreementBox>
                By proceeding, I agree to Sojiare <Links to="/terms-and-conditions">Terms of Use</Links> and acknowledge
                that I have read the <Links to="/policy">Privacy Policy</Links>.
            </AgreementBox>
        </div>
    );
};

export default function Switch() {
    const [flip, setFlip] = useState(false);

    const switchCard = () => {
        setFlip(!flip);
    };
    return (
        <Structure homepage>
            <div className="scene scene--card">
                <div className={`container_card ${flip && 'is-flipped'}`}>
                    <div className="card__face card__face--front">
                        <SwitchButton onClick={() => switchCard()}><div>Search Companies and Links</div> <AiOutlineArrowRight color='#fff' size={17} /></SwitchButton>
                        <div className="col-md-12">
                            <>
                                <div className="content_desc">
                                    <div>
                                        Avoid falling victim to{' '}
                                        <span className="text_default_color text_bold">scammers' impersonation</span>
                                    </div>
                                    <p className="text_bold">Be a step ahead of Scammers</p>
                                </div>
                                <div className="content_wrapper">
                                    <div className="content_wrapper_box">
                                        <div className="content_body">
                                            <ScamSearch />
                                        </div>
                                        <div className="box_description">
                                            <Description />
                                        </div>
                                    </div>
                                </div>
                            </>
                        </div>
                    </div>
                    <div className="card__face card__face--back">
                        <SwitchButton onClick={() => switchCard()}><AiOutlineArrowLeft color='#fff' size={17} /> <div>Autheticate Contents</div> </SwitchButton>
                        <div className="col-md-12">
                            <>
                                <div className="content_desc">
                                    <div>
                                        Behind every review is an
                                        <span className="text_default_color text_bold"> experience that matters</span>
                                    </div>
                                    <p className="text_bold">Read reviews. Write reviews. Find companies.</p>
                                </div>
                                <div className="content_wrapper">
                                    <div className="content_wrapper_box">
                                        <div className="content_body">
                                            <CompanySearch />
                                        </div>
                                        <div className="box_description">
                                            <Description />
                                        </div>
                                    </div>
                                </div>
                            </>
                        </div>
                    </div>
                </div>
            </div>
        </Structure>
    );
}
